::-webkit-scrollbar {
  width: 5px;
  height: 5px;
}
::-webkit-scrollbar-thumb {
  border-radius: 1em;
  background-color: rgba(50, 50, 50, 0.3);
}
::-webkit-scrollbar-track {
  border-radius: 1em;
  background-color: rgba(50, 50, 50, 0.1);
}
.paddingLeft5 {
  padding-left: 5px;
}
.marginTop10 {
  margin-top: 10px;
}
.h3-ref {
  display: block;
  line-height: 44px;
  margin-bottom: 16px;
  padding-left: 10px;
  border-left: 4px solid #916dd5;
  background-color: #f8f8f8;
  color: #666;
}
.h4-ref {
  display: block;
  line-height: 32px;
  margin-bottom: 16px;
  padding-left: 10px;
  border-left: 4px solid #916dd5;
  background-color: #f8f8f8;
  color: #666;
}
.form-item {
  height: 56px;
}
.required::before {
  color: #ff4d4f;
  content: '*';
  display: inline-block;
  font-size: 14px;
  line-height: 1;
  margin-right: 4px;
}
.form-item > label {
  display: inline-block;
  margin-right: 5px;
  width: 80px;
  text-align: right;
  font-size: 14px;
}
.form-item > input {
  height: 32px;
  border: 1px solid #d9d9d9;
  border-radius: 3px;
  width: 300px;
  overflow: visible;
  font-size: 14px;
}
.primary-button {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
  min-width: 64px;
  width: 70px;
  line-height: 1.75;
  height: 32px;
  color: #fff;
  background-color: #1976d2;
  border: 1px solid #d9d9d9;
  border-radius: 4px;
  font-size: 14px;
  cursor: pointer;
}
