.sider {
	height: 100vh;

	.sider-menus {
		height: 90vh;
		overflow: auto;
	}

	.sider-menus::-webkit-scrollbar {
		display: none;
	}

	.sider-logo {
		display: flex;
		align-items: center;
		justify-content: center;
		height: 64px;
		background: #1166b0;
		color: #fff;
		font-size: 17px;
		text-align: center;
		cursor: pointer;
		a {
			color: inherit;
		}

		.sider-logo-img {
			margin-right: 4px;
		}
	}
}

.layout {
	height: 100vh;
	background: #fff;

	.layout-header {
		background: #1166b0;
		color: #fff;

		.trigger {
			color: #fff;
			padding: 0 24px;
			font-size: 18px;
			line-height: 64px;
			cursor: pointer;
			transition: color 0.3s;
		}

		.trigger:hover {
			color: #1890ff;
		}
	}

	.layout-content {
		margin: 20px;
		padding: 20px;
		background-color: #fff;
		overflow: auto;
	}
}

@primary-menu-color: #7638ff;@primary-menu-background-color: rgba(118, 56, 255, 0.05);@darker-color: rgba(0,0,0,0.85);@dark-color: rgba(0,0,0,0.65);@light-color: rgba(0,0,0,0.45);@layout-header-background: #FFFFFF;@font-family: PingFang SC;@card-shadow: 0px 10px 20px 0px rgba(151,151,151,0.12);@border: solid 1px rgba(0,0,0,0.1);@border-radius-base: 4px;